@import url(https://fonts.googleapis.com/css2?family=Baloo+Thambi+2&family=Nunito:wght@300&family=Pompiere&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Baloo+Thambi+2&family=Nunito:wght@300&family=Pompiere&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Pompiere", cursive;
}

.header {
  background-color: black;
  height: 100%;
  width: 100%;
  margin: 0;
  padding: 40px 0;
  color: white;
}
.header__hero-container {
  padding-top: 5px;
}
.title-header {
  text-align: center;
  font-weight: lighter;
  letter-spacing: 4px;
  text-align: center;
}

.title-header-segundo {
  text-align: center;
  font-weight: bold;
  margin-bottom: 60px;
  letter-spacing: 1px;
}

.header__image-container {
  width: auto;
  height: auto;
  text-align: center;
  overflow: hidden;
}

.image_principal {
  width: 100%;
}

.hero__container {
  margin: 30px 30px;
}

.hero__parrafo {
  margin: 20px 0 40px 0;
  font-size: 20px;
}

/* -------contactos------- */

.hero__contacto {
  margin: 40px 0;
}

.hero__contacto h2 {
  margin: 30px 0;
}

.hero__contacto-content {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  margin-top: 10px;
}
.hero__contacto-content img {
  width: 20px;
}

.hero__contacto-content h3 {
  text-align: center;
  margin-left: 15px;
}

/* -------footer-------------- */
.container_footer h2 {
  margin: 30px 0;
}

.container_footer ul {
  padding-left: 20px;
}

.container_footer ul li {
  padding-left: 10px;
  font-size: 20px;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: "Pompiere", cursive;
}

.contenedor {
  width: 100%;
  background-color: white;
}
.container__hero {
  display: grid;
  grid-template-columns: 80% 20%;
  height: 60px;
  width: 100%;
}
.container__hero_barra {
  border-bottom: 5px solid black;
  margin: 0;
  padding: 0;
}
/* ------------------contenedor------------------- */
.hero__contenedor {
  padding: 20px 50px;
}

.hero__contenedor_hero {
  margin: 40px 0;
  border: 1px solid black;
  border-radius: 10px;
  padding: 10px;
}

.hero__contenedor_hero h3 {
  margin: 3px 0;
  font-size: 20px;
  letter-spacing: 1px;
}

.hero__contenedor_hero a {
  text-decoration: none;
  color: black;
  font-weight: bold;
}

.hero__contenedor_hero a:hover {
  color: blue;
}

.hero__contenedor_hero p {
  margin: 20px 0;
  font-size: 20px;
}

/* ----------------ESTUDIO------------------------- */
.hero__contenedor_estudio {
  margin: 10px 0;
}

.hero__contenedor_estudio h2 {
  margin: 20px 0;
}

.hero__contenedor_estudio p {
  font-size: 20px;
}
/* ----------referencias---------- */
.hero__contenedor_referencias {
  margin: 10px 0;
}

.hero__contenedor_referencias h2 {
  margin: 20px 0;
}

.hero__contenedor_referencias p {
  font-size: 20px;
}
/* --------barra abajo----- */
.footer {
  position: absolute;
  bottom: 0;
  right: 0;
  display: grid;
  grid-template-columns: 20% 80%;
  grid-column-start: 2;
  grid-column-end: 3;
  height: 60px;
  width: 100%;
  margin: 0;
  padding: 0;
}
.container__hero_barra-abajo {
  background-color: black;
  text-align: center;
  -webkit-align-items: center;
          align-items: center;
  display: -webkit-flex;
  display: flex;
}

.container__hero_barra-abajo a {
  margin: 10px;
  border-radius: 30px;
}

.container__hero_barra-abajo img {
  height: 25px;
  width: 25px;
}

.container__app {
  display: grid;
  grid-template-columns: 40% 60%;
  width: 80%;
  margin: auto;
  min-height: 100%;
  position: relative;
}
@media only screen and (max-width: 600px) {
  .container__app {
    display: block;
  }
  .hero__contenedor {
    height: 1540px;
  }
}

